form {
  max-width: 300px;
  margin: 20px auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  position: relative;
  overflow: hidden;
}

label {
  display: block;
  margin-bottom: 10px;
  font-weight: 600;
  color: #4cae10;
}

input {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  box-sizing: border-box;
  border: 1px solid #4cae10;
}

button {
  background-color: #4caf50;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

/* MainOut 컴포넌트 스타일 */
div {
  margin-top: 20px;
  text-align: center;
}

h2 {
  color: green;
}

p {
  margin: 10px 0;
  color: #4cae10;
  font-weight: 600;
}

